import React from "react";
import { BrowserRouter as Router } from "react-router-dom";
import { HashLink as Link } from "react-router-hash-link";
import { Button, Navbar, Nav } from "react-bootstrap";

const Enlaces = [
  {
    enlace: "Inicio",
    url: "#Inicio",
  },
  {
    enlace: "Mis tecnologías",
    url: "#Tecnologias",
  },
  {
    enlace: "Estudios",
    url: "#Estudios",
  },
  {
    enlace: "Contacto",
    url: "#SobreMi",
  },
];

export default function Navegacion() {
  return (
    <Router>
      <Navbar
        collapseOnSelect
        expand="lg"
        bg="dark"
        variant="dark"
        className="container-navegacion"
      >
        <Navbar.Brand></Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="container-botones">
            {Enlaces.map((enlace, key) => {
              return (
                <Link to={enlace.url} key={key} className="btn">
                  <Button variant="dark">
                    <h5 className="titulo-enlace">{enlace.enlace}</h5>
                  </Button>
                </Link>
              );
            })}
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    </Router>
  );
}
