import React from "react";

import logoReact from "../images/logosPiePagina/logoPie.svg";
import logoHtml from "../images/logosPiePagina/html5.svg";
import logoCss from "../images/logosPiePagina/css3.svg";

export default function Footer() {
  return (
    <div className="container-pie-pagina">
      <div className="pie-p-container1">
        <img
          src={logoReact}
          className="logoPiePagina"
          alt="logo React JS"
        ></img>
        <img src={logoHtml} alt="logo HTML5" className="logoPiePagina"></img>
        <img src={logoCss} alt="logo CSS3" className="logoPiePagina"></img>
      </div>
      <div>
        Copyright &copy; 2020 All Rights Reserved by Alexis L Rumbo
        <p>
          Iconos diseñados por _
          <a
            href="https://www.flaticon.es/autores/pixel-perfect"
            title="Pixel perfect"
          >
            Pixel perfect _
          </a>
          from _
          <a href="https://www.flaticon.es/" title="Flaticon">
            www.flaticon.es
          </a>
        </p>
      </div>
    </div>
  );
}
