import React from "react";

import { Card, Button } from "react-bootstrap";
//iconos
import iconoFb from "../images/iconos/facebook.svg";
import icono3Insta from "../images/iconos/instagram.svg";
import icono4Link from "../images/iconos/linkedin.svg";

export default function Descripcion() {
  return (
    <div className="container-description" id="SobreMi">
      <Card className="tarjeta-description">
        <Card.Body>
          <Card.Title>
            <h3>Sobre Mi</h3>
          </Card.Title>
          <Card.Text>
            Estoy especializándome en el desarrollo web, sobre todo en la
            interacción con el usuario o front end. Tengo proyectos propios
            imitados y realizados con HTML5, CSS3 Y Javascript. No tengo
            experiencia trabajando como programador web pero sí estoy interesado
            en ampliar mis conocimentos y aplicarlo en mis trabajos.
          </Card.Text>
          <Card.Title>
            <h4>Contacto</h4>
          </Card.Title>
          <Card.Text>
            <a
              href="https://www.facebook.com/profile.php?id=693242853"
              target="blank"
            >
              <img src={iconoFb} alt="" className="iconoDescription"></img>
            </a>
            <a href="https://www.instagram.com/lrumbo/" target="blank">
              <img src={icono3Insta} alt="" className="iconoDescription"></img>
            </a>
            <a
              href="https://www.linkedin.com/in/alexis-lrumbo-a68208a3/"
              target="blank"
            >
              <img src={icono4Link} alt="" className="iconoDescription"></img>
            </a>
          </Card.Text>
          <a
            href="https://drive.google.com/file/d/1yJJeO66ZkhWZY12a1DCDHIw9BUHseN8t/view?usp=sharing"
            target="blank"
          >
            <Button variant="primary" className="btn-description">
              Descargar CV
            </Button>
          </a>
        </Card.Body>
      </Card>
    </div>
  );
}
