import React from "react";
import { Table } from "react-bootstrap";

export default function Tabla() {
  return (
    <Table className="table" striped bordered hover variant="dark">
      <thead className="table-head" id="Tecnologias">
        <tr>
          <th className="container-aboutMe" colSpan="3">
            <h4>Las técnologias más aprendidas y utilizadas por mi:</h4>
          </th>
        </tr>
        <tr>
          <th>Tecnologías</th>
          <th>Nivel</th>
          <th>Cantidad de proyectos propios realizados</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>HTML5</td>
          <td>Intermedio</td>
          <td>3</td>
        </tr>
        <tr>
          <td>CSS3</td>
          <td>Intermedio</td>
          <td>3</td>
        </tr>
        <tr>
          <td>Web Responsive</td>
          <td>Básico</td>
          <td>2</td>
        </tr>
        <tr>
          <td>Javascript</td>
          <td>Básico</td>
          <td>3</td>
        </tr>
        <tr>
          <td>React JS</td>
          <td>Básico</td>
          <td>1</td>
        </tr>
        <tr>
          <td>GIT</td>
          <td>Comandos básicos</td>
          <td>2</td>
        </tr>
      </tbody>
    </Table>
  );
}
