import React from "react";
import { Card, ListGroup } from "react-bootstrap";

export default function Estudios() {
  return (
    <div className="container-estudios" id="Estudios">
      <Card className="container-tarjet">
        <Card.Body>
          <Card.Title>
            <h3 className="tarjet-title">Más sobre mis estudios</h3>
          </Card.Title>
          <Card.Body>
            Recibido de la Universidad Tecnológica Nacional en el año 2015.
          </Card.Body>
          <Card.Body>
            Allí aprendí otros lenguajes de programación como:
          </Card.Body>
          <ListGroup variant="flush" className="container-list">
            <ListGroup.Item className="item-list" variant="info">
              C
            </ListGroup.Item>
            <ListGroup.Item className="item-list" variant="light">
              C++
            </ListGroup.Item>
            <ListGroup.Item className="item-list" variant="info">
              C#
            </ListGroup.Item>
            <ListGroup.Item className="item-list" variant="light">
              MySql
            </ListGroup.Item>
            <ListGroup.Item className="item-list" variant="info">
              ASP.NET
            </ListGroup.Item>
            <ListGroup.Item className="item-list" variant="light">
              Javascript en la WEB
            </ListGroup.Item>
          </ListGroup>
        </Card.Body>
      </Card>
    </div>
  );
}
