import React from "react";
import Imagen from "../images/banner.jpg";
import Tabla from "../components/Tabla";

export default function Aboutme() {
  return (
    <div className="container-table" id="Inicio">
      <div className="container-principal-table">
        <img
          src={Imagen}
          className="container-principal-imagen"
          alt="imagen programacion"
        ></img>
        <div className="container-principal-titulo">
          <div className="container-principal-titulo-flex">
            <h1>Alexis L Rumbo</h1>
            <h2>Frontend Developer</h2>
          </div>
        </div>
      </div>
      <Tabla />
    </div>
  );
}
